import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import * as LIB from '../../numerical-methods';

import * as math from 'mathjs';


@Component({
  selector: 'app-false-position',
  templateUrl: './false-position.component.html',
  styleUrls: ['./false-position.component.css']
})
export class FalsePositionComponent implements OnInit {

  xr: number;
  fxr: number;

  model = new LIB.NumericalMethods(
    -10, +10, 0.01, 100, 'x^3-2x-5'
  );

  constructor() { }

  ngOnInit() {
  }


  performEquation(number: number, formula: String) {
    const scope = { x: number }; 
    return math.evaluate(formula, scope);
  }

  modFalsePos(xl: number, xu: number, es: number, imax: number, formula: String) {
    let iter = 0;
    let fl = this.performEquation(xl, formula);
    let fu = this.performEquation(xu, formula);
    let il = 0;
    let iu = 0;
    let xr = null;  /** */
    let ea = 0;

    while ( ea >= es || iter < imax) {
      let xrold = xr;
      xr = xu - fu * (xl - xu) / (fl - fu);
      let fr = this.performEquation(xr, formula);
      iter++;
      
      if ( xr != 0 ) {
        ea = Math.abs((xr - xrold) / xr) * 100;
      }

      let test = fl * fr;

      if ( test < 0 ) {
        xu = xr;
        fu = this.performEquation(xu, formula);
        iu = 0;
        il++;

        if ( il >= 2 ) {
          fl = fl / 2;
        }
      } else if ( test > 0 ) {
        xl = xr;
        fl = this.performEquation(xl, formula);
        il = 0;
        iu++;

        if ( iu >= 2 ) {
          fu = fu / 2;
        }
      } else {
        ea = 0;
      }
    }

    return xr;
  }

  // algorithm for false position yet to be implemented .....
  onSubmit() { 
    this.xr = this.modFalsePos(
      this.model.xl,
      this.model.xu,
      this.model.es,
      this.model.imax,
      this.model.formula
    );
    this.fxr = this.performEquation(this.xr, this.model.formula);
  }

}
