import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './components/pages/home/home.component';
import { BisectionComponent } from './components/bisection/bisection.component';
import { FalsePositionComponent } from './components/false-position/false-position.component';
import { FormulasComponent } from './components/formulas/formulas.component';
import { SecantComponent } from './components/secant/secant.component';
import { GraphColoringComponent } from './components/graph-coloring/graph-coloring.component';
import { NewtonRaphsonComponent } from './newton-raphson/newton-raphson.component';
import { PrimComponent } from './prim/prim.component';
import { DijkstraComponent } from './dijkstra/dijkstra.component';
import { GraphColoringStepsComponent } from './graph-coloring-steps/graph-coloring-steps.component';
// import { KruskalComponent } from './kruskal/kruskal.component';
import { KruskalComponent } from './kruskal/kruskal.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'bisection', component: BisectionComponent },
  { path: 'false-position', component: FalsePositionComponent },
  { path: 'formula', component: FormulasComponent },
  { path: 'secant-method', component: SecantComponent },
  { path: 'newton-method', component: NewtonRaphsonComponent },
  { path: 'graph-coloring', component: GraphColoringComponent },
  { path: 'prim', component: PrimComponent },
  { path: 'dijkstra', component: DijkstraComponent },
  { path: 'graph-coloring-steps', component: GraphColoringStepsComponent },

  { path: 'kruskal', component: KruskalComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
exports: [RouterModule]
})
export class AppRoutingModule { }
